@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
:root {
  --twitter-blue: rgb(29, 161, 242);
  --twitter-black: rgb(20, 23, 26);
  --twitter-dark-grey: rgb(101, 119, 134);
  --twitter-light-grey: rgb(170, 184, 194);
  --twitter-extra-light-grey: rgb(224, 232, 237);
  --twitter-extra-extra-light-grey: rgb(245, 248, 250);
}

.App {
  display: flex;
  justify-content: center;
  padding-top: 30px;
}

.bio-link {
  color: rgb(29, 161, 242);
  color: var(--twitter-blue);
}

.bio-link:hover {
  text-decoration: underline;
}

.app__small-text-hover-card {
  background: rgb(0,0,0,0.6);
  color: white;
  position: absolute;
  font-size: 11px;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: auto;
  white-space: nowrap;
  padding: 2px;
  text-align: center;
}

* {
    font-family: 'Roboto', sans-serif;
}

.card {
    display: grid;
    grid-template-rows: 30px auto 40px;
    grid-template-columns: 70px 300px;
    grid-row-gap: 3px;
    row-gap: 3px;
    border: 1px solid lightgrey;
    padding: 10px 15px 0px 15px;
    width: 500px;
    position: relative;
}

.card:hover {
    background: var(--twitter-extra-extra-light-grey);
}

.card__body {
    grid-column: 2 / 4;
    grid-row: 2 / 3;
    font-size: 13px;
}

.card__profile {
    grid-row: 1 / 3;
    grid-column: 1 / 2;
    width: 50px;
    border-radius: 50px;
    border: 1px solid var(--twitter-dark-grey);
}

.card__profile:hover {
    opacity: 0.8;
    box-shadow: inset 0px 0px 5px 5px black;
}
.hoverCard__bio {
    white-space: pre-wrap;
    font-size: 15px;
}

.hoverCard__button {
    grid-column: 2 / 3;
    height: 35px;
    width: 85px;
    border-radius: 25px;
    font-size: 15px;
    letter-spacing: 0.4px;
    font-weight: bold;
    padding: 0px;
    margin: 0px;
    justify-self: end;
}

.hoverCard__follow-button {
    background-color: var(--twitter-black);
    border: 1px solid var(--twitter-black);
    color: white;
}

.hoverCard__follow-button:hover {
    opacity: 0.8;
}

.hoverCard__following-button {
    border: 1px solid var(--twitter-light-grey);
    background-color: white;
}

.hoverCard__following-button:hover {
    border: 1px solid rgb(249, 24, 128, 0.2);
    background-color: rgb(249, 24, 128, 0.1);
    color: rgb(249, 24, 128);
}

.hoverCard__container {
    position: absolute;
    background-color: white;
    width: 230px;
    height: 250px;
    border-radius: 15px;
    opacity: 0;
    box-shadow: 0px 0.5px 1px 1px rgb(0, 0, 0, 0.1);
    display: grid;
    grid-template-rows: repeat(auto, 6);
    padding: 15px;
}

.hoverCard__display-picture {
    grid-row: 1 / 3;
    grid-column: 1 / 2;
    width: 60px;
    border-radius: 50px;
    border: 1px solid var(--twitter-dark-grey);
}

.hoverCard__display-picture:hover {
    opacity: 0.8;
    box-shadow: inset 0px 0px 5px 5px black;
}

.hoverCard__footer {
    font-size: 12px;
    color: var(--twitter-dark-grey);
    margin-top: 25px;
    grid-row: 4 / 5;
    grid-column: 1 / 3;
}

.hoverCard__header {
    display: grid;
    grid-template-rows: repeat(2, -webkit-min-content);
    grid-template-rows: repeat(2, min-content);
    grid-template-columns: repeat(2, auto);
    grid-row-gap: 3px;
    row-gap: 3px;
}

.hoverCard__name-verified {
    display: grid;
    grid-column: 1 / 3;
    grid-row: 4 / 5;
    grid-template-columns: -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content min-content;
    grid-template-rows: repeat(2, -webkit-min-content);
    grid-template-rows: repeat(2, min-content);
    grid-row-gap: 3px;
    row-gap: 3px;
    white-space: nowrap;
    grid-column-gap: 3px;
    -webkit-column-gap: 3px;
            column-gap: 3px;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    align-items: center;
}

.hoverCard__name-verified:hover > .card__header-name {
    text-decoration: underline;
}

.hoverCard__profile {
    top: 80px;
    left: 15px;
}

.hoverCard__stats {
    font-size: 15px;
    color: var(--twitter-dark-grey);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row: 3 / 4;
}

.hoverCard__stats-element-following {
    position: absolute;
    grid-column: 1 / 2;
}

.hoverCard__stats-element-followers {
    position: absolute;
    grid-column: 2 / 3;
}

.hoverCard__stats-element-following:hover, .hoverCard__stats-element-followers:hover {
    text-decoration: underline;
}

.hoverCard__stats-value {
    color: black;
    font-weight: bold;
}

.hoverCard__top {
    top: 40px;
    left: -10px;
}
.card__header {
    display: flex;
    justify-content: space-between;
    grid-column: 2 / 4;
    grid-row: 1 / 2;
    align-items: center;
    width: 100%;
    position: relative;
}

.card__header-circle {
    width: 2px;
    align-self: center;
}

.card__header-date {
    position: relative;
}

.card__header-date:hover {
    text-decoration: underline;
}

.card__header-identity {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-column-gap: 3px;
    -webkit-column-gap: 3px;
            column-gap: 3px;
}

.card__header-identity:hover > .card__header-name {
    text-decoration: underline;
}

.card__header-info {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-column-gap: 3px;
    -webkit-column-gap: 3px;
            column-gap: 3px;
}

.card__header-name {
    font-weight: bold;
    font-size: 14px;
    color: var(--twitter-black);
    letter-spacing: 0.4px;
}

.card__header-text {
    color: var(--twitter-dark-grey);
    font-size: 13px;
}

.card__header-verified {
    color: var(--twitter-blue);
    align-self: center;
}

.card__footer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-row: 3 / 4;
    grid-column: 2 / 4;
    grid-column-gap: 3px;
    -webkit-column-gap: 3px;
            column-gap: 3px;
}

.card__footer>div {
    display: grid;
    grid-template-columns: auto auto;
}

.card__footer>div>div {
    display: grid;
    grid-template-columns: 25px auto;
    grid-column-gap: 8px;
    -webkit-column-gap: 8px;
            column-gap: 8px;
    font-size: 12px;
    color: var(--twitter-dark-grey);
    align-items: center;
}

.card__footer>div:hover .card__footer-icon, .card__header-options:hover {
    border-radius: 7px;
}

.card__footer-heart:hover {
    background-position: right;
}

.card__footer-icon {
    width: 18px;
    height: 18px;
    align-self: center;
    justify-self: center;
}

.card__footer-comment-element:hover, .card__footer-share:hover, .card__header-options:hover {
    color: var(--twitter-blue);
}

.card__footer-comment-element:hover > .card__footer-icon, 
.card__footer-share:hover > .card__footer-icon,
.card__header-options:hover {
    box-shadow: inset 0px 0px 10px 5px rgb(29, 161, 242, 0.2),
                0px 0px 3px 7px rgb(29, 161, 242, 0.2);
}

.card__footer-like-element:hover {
    color: rgb(249, 24, 128);
}

.card__footer-like-element:hover > .card__footer-icon {
    box-shadow: inset 0px 0px 10px 5px rgb(249, 24, 128, 0.2),
    0px 0px 3px 7px rgb(249, 24, 128, 0.2);
}

.card__footer-retweet-element:hover {
    color: rgb(0, 186, 124);
}

.card__footer-retweet-element:hover > .card__footer-icon {
    box-shadow: inset 0px 0px 10px 5px rgb(0, 186, 124, 0.2),
                    0px 0px 3px 7px rgb(0, 186, 124, 0.2);
}
.successToaster {
    background-color: var(--twitter-blue) !important;
}

.toaster {
    color: white !important;
    font-weight: bolder;
    font-size: 14px;
    text-align: center;
    min-height: -webkit-min-content !important;
    min-height: -moz-min-content !important;
    min-height: min-content !important;
}

.unfinishedFeatureToaster {
    background-color: red !important;
    overflow: visible;
    white-space: nowrap;
}
.menu__container {
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    position: absolute;
    border: 1px solid black;
    background-color: white;
    border: 1px solid var(--twitter-light-grey);
    border-radius: 3px;
    box-shadow: 0px 0.5px 1px 1px rgb(0, 0, 0, 0.1);
}
.option__container {
    display: grid;
    grid-template-columns: -webkit-min-content auto;
    grid-template-columns: min-content auto;
    grid-template-rows: 40px;
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
            column-gap: 15px;
    align-items: center;
    padding: 5px 15px 5px 15px;
}

.option__container:hover {
    background: var(--twitter-extra-extra-light-grey);
}

.option__icon-container {
    align-items: center;
}

.option__icon-container>* {
    width: 20px;
    height: 20px;
    color: var(--twitter-dark-grey);
}

.option__text {
    align-items: center;
    font-size: 15px;
}
.optionsMenu__container {
    top: 15px;
    right: 0px;
    width: 350px;
}
.shareMenu__container {
    top: 80px;
    right: 80px;
}
.retweetMenu__container {
    top: 80px;
    left: 100px;
}
